*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  border: none;
  outline: none;
  box-sizing: border-box;
}

*::before,
*::after {
  display: block;
}

ul, ol {
  list-style: none;
}

a {
  text-decoration: none;
}

select {
  appearance: none;
}

h1, h2, h3, h4,
h5, h6, p,
ol, ul,
figure,
.table,
.form-row {
  margin: 0;
}

