/*-----------------
    1. GENERAL
-----------------*/
label,
input[type="text"],
input[type="email"],
input[type="password"],
textarea,
select,
button {
  font-family: "Rajdhani", sans-serif;
}

label {
  display: block;
  margin: 0;
  color: #fff;
  line-height: 1em;
}

input[type="text"],
input[type="email"],
input[type="password"],
textarea,
select,
button {
  width: 100%;
  border-radius: 12px;
  font-size: 1rem;
  font-weight: 700;
}

input[type="text"],
input[type="email"],
input[type="password"],
textarea,
select {
  background-color: #1d2333;
  border: 1px solid #3f485f;
  color: #fff;
  transition: border-color .2s ease-in-out;
}

input[type="text"]:focus,
input[type="email"]:focus,
input[type="password"]:focus,
textarea:focus,
select:focus {
  border-color: #28069a;
}

input[type="text"],
input[type="email"],
input[type="password"] {
  height: 54px;
  padding: 0 18px;
}

input[type="text"]::placeholder,
input[type="email"]::placeholder,
input[type="password"]::placeholder {
  color: #9aa4bf;
  font-size: 0.875rem;
  font-weight: 500;
}

textarea {
  padding: 14px 18px;
  resize: none;
}

textarea::placeholder {
  color: #9aa4bf;
  font-weight: 600;
}

/*--------------
    2. FORM
--------------*/
.form {
  width: 100%;
}

.form + .button {
  margin-top: 40px;
}

/*------------------
    3. FORM ROW
------------------*/
.form-row {
  width: 100%;
}

.form-row + .form-row {
  margin-top: 28px;
}

.form-row.space-between {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.form-row.space-between .form-item {
  width: auto;
}

.form-row.split {
  display: flex;
  flex-wrap: nowrap;
}

.form-row.split .form-item {
  margin-right: 16px;
}

.form-row.split .form-item:last-child {
  margin-right: 0;
}

.form-row.split .form-item > * {
  margin-bottom: 28px;
}

.form-row.split .form-item > *:last-child {
  margin-bottom: 0;
}

/*------------------
    4. FORM ITEM
------------------*/
.form-item {
  width: 100%;
}

.form-item.auto-width {
  width: auto;
}

.form-item.centered {
  display: flex;
  align-items: center;
}

.form-item.split {
  display: flex;
}

.form-item.split.medium > * {
  margin-right: 16px;
}

.form-item.split.medium > *:last-child {
  margin-right: 0;
}

.form-item.split > * {
  width: 100%;
  margin-right: 12px;
}

.form-item.split > *:last-child {
  margin-right: 0;
}

/*------------------------------
    5. FORM INPUT DECORATED
------------------------------*/
.form-input-decorated {
  position: relative;
}

.form-input-decorated input[type="text"] {
  padding-right: 60px;
}

.form-input-decorated .form-input-icon {
  fill: #adafca;
  opacity: .6;
  pointer-events: none;
  position: absolute;
  top: 14px;
  right: 20px;
}

/*-------------------
    6. FORM INPUT
-------------------*/
.form-input {
  position: relative;
}

.form-input.dark input[type="text"],
.form-input.dark input[type="password"],
.form-input.dark textarea {
  border: none;
  background-color: #1b036d;
  color: #fff;
}

.form-input.dark input[type="text"]::placeholder,
.form-input.dark input[type="password"]::placeholder,
.form-input.dark textarea::placeholder {
  color: #8b88ff;
  opacity: .6;
}

.form-input.social-input .social-link {
  position: absolute;
  top: 4px;
  left: 4px;
  pointer-events: none;
}

.form-input.social-input input {
  padding-left: 64px;
}

.form-input.social-input label {
  left: 64px;
}

.form-input.social-input.active label {
  left: 58px;
}

.form-input.currency-decorated::before {
  content: '$';
  color: #4ff461;
  font-size: 0.875rem;
  font-weight: 700;
  position: absolute;
  top: 12px;
  left: 22px;
  pointer-events: none;
}

.form-input.currency-decorated input {
  padding-left: 32px;
}

.form-input.small label,
.form-input.small input,
.form-input.small textarea {
  font-size: 0.875rem;
}

.form-input.small label {
  top: 16px;
}

.form-input.small input {
  height: 48px;
}

.form-input.small textarea {
  height: 100px;
}

.form-input.small button {
  width: 64px;
  position: absolute;
  top: 0;
  right: 0;
}

.form-input.full {
  height: 100%;
}

.form-input.full textarea {
  height: 100%;
}

.form-input.mid-textarea textarea {
  height: 124px;
}

.form-input.medium-textarea textarea {
  height: 160px;
}

.form-input.active label {
  background-color: #1d2333;
  padding: 0 6px;
  font-size: 0.75rem;
  top: -6px;
  left: 12px;
}

.form-input label {
  color: #9aa4bf;
  font-size: 1rem;
  font-weight: 600;
  position: absolute;
  top: 20px;
  left: 20px;
  transition: all .3s ease-in-out;
  pointer-events: none;
}

/*-------------------
    7. FORM TITLE
-------------------*/
.form-title {
  font-size: 0.875rem;
  font-weight: 700;
}

/*----------------------
    8. FORM TEXTAREA
----------------------*/
.form-textarea {
  position: relative;
}

.form-textarea textarea {
  font-size: 0.875rem;
  padding: 26px 28px 0;
}

.form-textarea .form-textarea-limit-text {
  padding: 0 28px;
  font-size: 0.75rem;
  font-weight: 500;
  opacity: .6;
  text-align: right;
}

/*----------------------
    9. FORM SELECT
----------------------*/
.form-select {
  width: 100%;
  height: 48px;
  position: relative;
}

.form-select.v2 {
  width: auto;
  height: auto;
}

.form-select.v2 select {
  width: auto;
  height: auto;
  padding-right: 20px;
  border: none;
  font-size: 0.75rem;
}

.form-select.v2 .form-select-icon {
  top: 10px;
  right: 0;
}

.form-select select {
  width: 100%;
  height: 100%;
  padding-left: 20px;
  font-size: 0.875rem;
  cursor: pointer;
}

.form-select .form-select-icon {
  fill: #fff;
  transform: rotate(90deg);
  position: absolute;
  top: 20px;
  right: 20px;
  pointer-events: none;
}

.form-select label {
  padding: 0 6px;
  background-color: #1d2333;
  color: #9aa4bf;
  font-size: 0.75rem;
  font-weight: 600;
  position: absolute;
  top: -8px;
  left: 12px;
  pointer-events: none;
}

/*------------------------
    10. CHECKBOX LINE
------------------------*/
.checkbox-line {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.checkbox-line .checkbox-line-text {
  color: #4ff461;
  font-size: 0.875rem;
  font-weight: 500;
}

/*------------------------
    11. CHECKBOX WRAP
------------------------*/
.checkbox-wrap {
  position: relative;
}

.checkbox-wrap .checkbox-info {
  margin-top: 12px;
}

.checkbox-wrap .checkbox-info .checkbox-info-text {
  font-size: 0.875rem;
  font-weight: 500;
  line-height: 1.4285714286em;
}

.checkbox-wrap.checkbox-rating .rating-list {
  position: absolute;
  top: 6px;
  left: 34px;
  pointer-events: none;
}

.checkbox-wrap.checkbox-rating label {
  padding-left: 104px;
}

.checkbox-wrap input[type="checkbox"],
.checkbox-wrap input[type="radio"] {
  display: none;
}

.checkbox-wrap input[type="checkbox"]:checked + .checkbox-box,
.checkbox-wrap input[type="radio"]:checked + .checkbox-box {
  background-color: #40d04f;
  border-color: #40d04f;
}

.checkbox-wrap input[type="checkbox"]:checked + .checkbox-box.round,
.checkbox-wrap input[type="radio"]:checked + .checkbox-box.round {
  border: 6px solid #40d04f;
  background-color: #1d2333;
}

.checkbox-wrap input[type="checkbox"]:checked + .checkbox-box .icon-cross,
.checkbox-wrap input[type="radio"]:checked + .checkbox-box .icon-cross {
  fill: #1d2333;
}

.checkbox-wrap label {
  padding-left: 34px;
  font-size: 0.875rem;
  font-weight: 700;
  line-height: 22px;
  cursor: pointer;
}

.checkbox-wrap .checkbox-box {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 22px;
  height: 22px;
  border: 1px solid #3f485f;
  border-radius: 6px;
  background-color: #1d2333;
  pointer-events: none;
  position: absolute;
  top: 0;
  left: 0;
  transition: border-color .2s ease-in-out, background-color .2s ease-in-out;
}

.checkbox-wrap .checkbox-box.round {
  border-radius: 50%;
}

.checkbox-wrap .checkbox-box .icon-cross {
  fill: transparent;
  transition: fill .2s ease-in-out;
}

.checkbox-wrap.small label {
  padding-left: 24px;
  font-size: 0.75rem;
  line-height: 16px;
}

.checkbox-wrap.small .checkbox-box {
  width: 16px;
  height: 16px;
  border-radius: 4px;
}

.checkbox-wrap.small .checkbox-box.round {
  border-radius: 50%;
}

.checkbox-wrap.small .checkbox-box .icon-cross {
  width: 8px;
  height: 8px;
}

/*--------------------
    12. FORM LINK
--------------------*/
.form-link {
  color: #adafca;
  font-size: 0.875rem;
  font-weight: 700;
  line-height: 22px;
}

/*--------------------
    13. FORM TEXT
--------------------*/
.form-text {
  font-size: 0.875rem;
  line-height: 1.7142857143em;
  font-weight: 500;
  font-weight: 500;
}

/*----------------------------
    14. INTERACTIVE INPUT
----------------------------*/
.interactive-input {
  width: 100%;
  height: 52px;
  position: relative;
}

.interactive-input.dark input {
  border: none;
  background-color: #1b036d;
  color: #fff;
}

.interactive-input.dark input::placeholder {
  color: #8b88ff;
  opacity: .6;
}

.interactive-input.dark .interactive-input-icon-wrap .interactive-input-icon {
  fill: #9b7dff;
}

.interactive-input.dark .interactive-input-action:hover .interactive-input-action-icon {
  fill: #fff;
}

.interactive-input.dark .interactive-input-action .interactive-input-action-icon {
  fill: #9b7dff;
}

.interactive-input.small {
  height: 48px;
}

.interactive-input.active .interactive-input-action {
  display: flex;
}

.interactive-input.active .interactive-input-icon {
  display: none;
}

.interactive-input input {
  height: 100%;
  padding-right: 60px;
}

.interactive-input .interactive-input-icon-wrap {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 60px;
  height: 100%;
  position: absolute;
  top: 0;
  right: 0;
  pointer-events: none;
}

.interactive-input .interactive-input-icon-wrap.actionable {
  pointer-events: all;
}

.interactive-input .interactive-input-icon-wrap.actionable .interactive-input-icon {
  cursor: pointer;
}

.interactive-input .interactive-input-icon-wrap.actionable .interactive-input-icon:hover {
  fill: #fff;
  opacity: 1;
}

.interactive-input .interactive-input-icon-wrap .interactive-input-icon {
  fill: #616a82;
  opacity: .6;
  transition: fill .2s ease-in-out, opacity .2s ease-in-out;
}

.interactive-input .interactive-input-action {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 60px;
  height: 100%;
  position: absolute;
  top: 0;
  right: 0;
  cursor: pointer;
  display: none;
}

.interactive-input .interactive-input-action:hover .interactive-input-action-icon {
  fill: #fff;
  opacity: 1;
}

.interactive-input .interactive-input-action .interactive-input-action-icon {
  fill: #616a82;
  opacity: .6;
  pointer-events: none;
  transition: fill .2s ease-in, opacity .2s ease-in;
}

/*----------------------------
    15. FORM ADD ITEMS
----------------------------*/
.form-add-items .form-add-items-title {
  color: #adafca;
  font-size: 0.75rem;
  font-weight: 600;
}

.form-add-items .form-add-items-info {
  margin-top: 12px;
  display: flex;
  align-items: center;
}

.form-add-items .form-add-items-info .user-avatar-list {
  margin-left: 14px;
}

.form-add-items .form-add-items-button {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 40px;
  height: 40px;
  border-radius: 8px;
  background-color: #4ff461;
  cursor: pointer;
}

.form-add-items .form-add-items-button .form-add-items-button-icon {
  pointer-events: none;
}

/*----------------------------
    16. FORM RATING WRAP
----------------------------*/
.form-rating-wrap {
  display: flex;
  align-items: center;
}

.form-rating-wrap label {
  margin-right: 16px;
  font-size: 0.875rem;
  font-weight: 700;
}

/*----------------------------
    17. FORM COUNTER WRAP
----------------------------*/
.form-counter-wrap {
  position: relative;
}

.form-counter-wrap label {
  padding: 0 6px;
  background-color: #1d2333;
  color: #9aa4bf;
  font-size: 0.75rem;
  font-weight: 600;
  position: absolute;
  top: -6px;
  left: 12px;
  z-index: 2;
  pointer-events: none;
}

/*----------------------
    18. FORM COUNTER
----------------------*/
.form-counter {
  display: flex;
  align-items: center;
  width: 96px;
  height: 48px;
  padding: 0 40px 0 18px;
  border: 1px solid #3f485f;
  border-radius: 12px;
  position: relative;
}

