  }
  .header .header-actions .navigation {
    display: none;
  }
  .header .header-actions .progress-stat {
    margin-right: 28px;
  }
  .header .header-actions.search-bar {
    width: 50%;
    margin-right: 26px;
  }
}

@media screen and (max-width: 960px) {
