  }
}

@media screen and (max-width: 480px) {
  .floaty-bar .bar-actions:nth-child(1) {
    display: none;
  }
  .floaty-bar .action-list {
    padding: 0;
  }
  .floaty-bar .action-list::before, .floaty-bar .action-list::after {
    display: none;
  }
  .floaty-bar .action-list .action-list-item {
    padding: 0 20px;
