  border-top-left-radius: 12px;
  border-bottom-left-radius: 12px;
}

.section-pager-bar .section-pager-controls .slider-control.right {
  right: 0;
  border-top-right-radius: 12px;
  border-bottom-right-radius: 12px;
}

/*------------------------
    8. SECTION PAGER
------------------------*/
.section-pager {
  display: flex;
  align-items: center;
}

.section-pager.secondary .section-pager-item.active .section-pager-item-text, .section-pager.secondary .section-pager-item:hover .section-pager-item-text {
  color: #28069a;
}

.section-pager .section-pager-item {
  padding: 0 8px;
  cursor: pointer;
  position: relative;
}

.section-pager .section-pager-item::before {
  content: '';
  width: 1px;
  height: 20px;
  background-color: #2f3749;
  position: absolute;
  top: 12px;
  left: 0;
}

.section-pager .section-pager-item:last-child::after {
  content: '';
  width: 1px;
  height: 20px;
  background-color: #2f3749;
  position: absolute;
  top: 12px;
  right: 0;
}

.section-pager .section-pager-item.active .section-pager-item-text, .section-pager .section-pager-item:hover .section-pager-item-text {
  background-color: #21283b;
  color: #4ff461;
  box-shadow: 3px 5px 20px 0 rgba(0, 0, 0, 0.12);
}

.section-pager .section-pager-item .section-pager-item-text {
  background-color: #1d2333;
  padding: 16px;
  border-radius: 12px;
  font-size: 0.875rem;
  font-weight: 700;
  transition: color .2s ease-in-out, box-shadow .2s ease-in-out, background-color .2s ease-in-out;
}

/*------------------------------
    9. SECTION RESULTS TEXT
------------------------------*/
.section-results-text {
  margin-top: -12px;
  color: #9aa4bf;
  font-size: 0.875rem;
  font-weight: 500;
}

/*-------------------------
    10. SECTION BANNER
-------------------------*/
.section-banner {
  min-height: 160px;
  padding: 52px 60px 0 200px;
  border-radius: 12px;
  background: url("../img/banner/banner-bg.png") no-repeat center;
  position: relative;
}

.section-banner .section-banner-icon {
  position: absolute;
  bottom: 0;
  left: 0;
}

.section-banner .section-banner-title,
.section-banner .section-banner-text {
  color: #fff;
}

.section-banner .section-banner-title {
  font-size: 2.25rem;
  font-weight: 700;
}

.section-banner .section-banner-text {
  margin-top: 10px;
  font-size: 1rem;
  font-weight: 500;
}

